    CRUD_Account_Get CRUD_Account_Get
    https://localhost:44341/api/FAMS/SP_JsonResult?storeproc=CRUD_Account_Get&parameters=1
========================================================================================== -->


<template>
  <div>
    <operator-sidebar
      :isSidebarActive="operatorSidebar"
      @closeSidebar="toggleDataSidebar"
      :data="sidebarData"
      @update="updateGrid"
      @add="addGrid"
    />
       <vx-card ref="MainCard2" v-show="!isInfoHidden">
        <div class="mt-5">
          <div class="vx-row">
              <div class="vx-col md:w-1/6 w-full">
                    <vs-button color="warning" type="border" class="mb-2"  @click="cancelAdd">Return</vs-button>
               </div>
                <div class="vx-col md:w-1/8 w-full">
                    <vs-button color="warning" type="border" class="mb-2"  @click="initValues">Restart</vs-button>
               </div>
          </div>
           
            <form-wizard  ref="wizard" color="rgba(var(--vs-primary), 1)"  errorColor="rgba(var(--vs-danger), 1)" :title="title" :subtitle="subtitle" finishButtonText="Save entry" @on-complete="formSubmitted">  
                <tab-content title="Step 1" class="mb-5" icon="feather icon-home" >
                    <!-- tab 1 content -->
                    <div class="vx-row">
 
                        <div class="vx-col md:w-1/2 w-full mt-5">
                            <vs-input label="Employee (less 9 characters)"  v-model="formData.name" class="w-full" />
                        </div>
                        <div class="vx-col md:w-1/2 w-full mt-5">
                            <vs-input label="Description"  v-model="formData.description" class="w-full" />
                        </div>
                      
                        <div class="vx-col md:w-1/2 w-full md:mt-8">
                            <div class="demo-alignment">
                                <span>Operator Download (No | Yes):</span>
                                <div class="flex">
                                  <vs-switch  color="success" v-model="formData.enable"></vs-switch>
                                </div>
                            </div>
                        </div>
                    </div>
                </tab-content>

                
                  <!-- tab 4 content -->
                <tab-content title="Step 2" class="mb-5" icon="feather icon-save">
                      <!-- <vx-card class="mt-base" no-shadow card-border> -->
                          <div class="vx-row"> 
                              <div class="vx-col md:w-1/2 w-full mt-5">
                                  <vs-input label="Employee's FAMS Tag" v-model="formData.tag" class="w-full" />
                              </div>  
                              <div class="vx-col md:w-1/2 w-full mt-5">
                                    <label class="w-full" size="small">Operator Type </label>
                                    <v-select class="w-full"  placeholder="Search / Select Operator Type"  :options="operatorTypeData"  
                                    v-model="operatorTypeSelect"  @search:blur="populateDataOperatorType"    name ="Search / Select Operator Type" />
                              </div>
                          </div>
                </tab-content>
            </form-wizard>
              
        </div> 
    </vx-card>
    <vx-card ref="MainCard">
      <div>
        <vs-table
          ref="gridTable"
          v-model="selected"
          pagination
          :max-items="itemsPerPage"
          search
          :data="operatorData"
          class="vs-con-loading__container"
        >
          <div slot="header" class="flex flex-wrap-reverse items-center flex-grow">
            <!-- ADD NEW -->
            <vs-button color="warning" type="gradient" @click="GetGrid()"        icon-pack="feather" icon="icon-refresh-ccw">Reload</vs-button>
            <vs-button color="success" type="gradient" @click="addVue"  icon-pack="feather" icon="icon-plus-square">Add New</vs-button>
            <!-- ITEMS PER PAGE -->
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-4">
              <div
                class="p-4 border border-solid border-grey-light rounded-full d-theme-dark-bg cursor-pointer flex items-center justify-between font-medium"
              >
                <span
                  class="mr-2"
                >{{ currentPage * itemsPerPage - (itemsPerPage - 1) }} - {{ operatorData.length - currentPage * itemsPerPage > 0 ? currentPage * itemsPerPage : operatorData.length }} of {{ operatorData.length }}</span>
                <feather-icon icon="ChevronDownIcon" svgClasses="h-4 w-4" />
              </div>
              <!-- <vs-button class="btn-drop" type="line" color="primary" icon-pack="feather" icon="icon-chevron-down"></vs-button> -->
              <vs-dropdown-menu>
                <vs-dropdown-item @click="itemsPerPage=10">
                  <span>10</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=20">
                  <span>20</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=50">
                  <span>50</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=100">
                  <span>100</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
          <template slot="thead">
            <vs-th sort-key="Tag">Tag</vs-th>
            <vs-th sort-key="Operator">Operator</vs-th>
            <vs-th sort-key="Type">Type</vs-th>
            <vs-th sort-key="Description">Description</vs-th>    
            <vs-th sort-key="Active">Active</vs-th>
            <vs-th>Action</vs-th>
          </template>
          <!-- visible: false -->
          <template slot-scope="{data}">
            <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">
              <vs-td :data="tr.tag">{{tr.tag}}</vs-td>
              <vs-td :data="tr.operator">{{tr.operator}}</vs-td>
              <vs-td :data="tr.operatorType">{{tr.operatorType}}</vs-td>
              <vs-td :data="tr.description">{{tr.description}}</vs-td>
              <vs-td :data="tr.enable">{{tr.enable}}</vs-td>
              <vs-td class="whitespace-no-wrap">
                <feather-icon
                  icon="EditIcon"
                  svgClasses="w-5 h-5 hover:text-primary stroke-current"
                  @click.stop="editDataSidebar(tr)"
                />
                <feather-icon
                  icon="TrashIcon"
                  svgClasses="w-5 h-5 hover:text-danger stroke-current"
                  class="ml-2"
                  @click.stop="deleteGrid(tr)"
                />
              </vs-td>
              <template class="expand-user" slot="expand" >
                <div class="con-expand-users w-full" >
                    <div>
                        <vs-table
                            ref="gridTable2"
                            v-model="selected"
                            :data="tr.perStore"                           
                          >
                            <template slot="thead">
                              <vs-th>Action</vs-th>
                              <vs-th>Download</vs-th>  
                              <vs-th>Store</vs-th> 
                              <vs-th>Tag</vs-th>  
                              <vs-th>Operator</vs-th>   
                            </template>
                            <!-- visible: false -->
                            <template slot-scope="{data}">
                              <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">
                              
                                <vs-td class="whitespace-no-wrap">
                                  <feather-icon
                                    icon="SaveIcon"
                                    svgClasses="w-5 h-5 hover:text-primary stroke-current"
                                    @click.stop="saveOperatorParameters(tr)"
                                  />
                                  <feather-icon
                                    icon="TrashIcon"
                                    svgClasses="w-5 h-5 hover:text-danger stroke-current"
                                    class="ml-2"
                                    @click.stop="deleteGrid(tr)"
                                  />
                                </vs-td>
                                  <vs-td :data="tr.download">
                                    <vs-switch v-model="tr.download">
                                        <span slot="on">Yes</span>
                                        <span slot="off">No</span>
                                      </vs-switch>
                                </vs-td>
                                <vs-td :data="tr.storeName">{{tr.storeName}}</vs-td>
                                <vs-td :data="tr.operatorTag">{{tr.operatorTag}}</vs-td>
                                 <vs-td :data="tr.operatorName">{{tr.operatorName}}</vs-td>
                              </vs-tr>
                            </template>
                          </vs-table>
                    </div>
                </div>
            </template>

            </vs-tr>
          </template>
        </vs-table>
      </div>
    </vx-card>
  </div>
</template>

<script>
import axios from "axios";
import flatPickr from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import vSelect from "vue-select";
import {FormWizard, TabContent} from 'vue-form-wizard';
import 'vue-form-wizard/dist/vue-form-wizard.min.css';
import operatorSidebar from "./OperatorSidebar.vue";
export default {
  data() {
    return {

      title:'FAMS - new Employee Wizard!!',
      subtitle:'Please complete each tab to add new Employee (operator/driver)',

      operatorTypeSelect:0,
      operatorTypeData:[],

      isListHidden: false,   
      isInfoHidden: true,

      itemsPerPage: 10,
      operatorSidebar: false,
      selected: [],
      operatorData: [],
      sidebarData: {},
      isMounted: false,
      formData: {
         id:0,
         accountId:0,
         operatorTypeId:0,
         operatorType: "None",
         name:"None",
         enable:true,
         description:"None",
         account:"",       
         tag  :"",       
         perOperatorDataStore:
          [{
            id:0,
            storeId:0,
            operatorId:0,
            download:0,
          }]
        }   
    };
  },
  components: {
    flatPickr,
    operatorSidebar,
    "v-select": vSelect,
     FormWizard,
     TabContent
  },
  computed: {
    currentPage() {
      if (this.isMounted) {
        return this.$refs.gridTable.currentx;
      }
      return 0;
    }
  },
  mounted() {
    this.GetGrid();
    this.isMounted = true;
  },
  methods: {
  //********************* System related*/
   successNotify() {
      this.$vs.notify({
        title: 'Successfully',
        text: 'Item successfully added | updated | removed',
        color: 'success'
      })
    },
  FailedNotify() {
      this.$vs.notify({
        title: 'Not Successfull',
        text: 'Item not added',
        color: 'danger'
      })
    },

    showLoadingOnElement(element, scale, type) {
      var self = this;
      self.$vs.loading({
        type: type,
        container: self.$refs[element].$el,
        scale: scale
      });
    },
    addDataSidebar() {
      this.sidebarData = {};
      this.toggleDataSidebar(true);
    },
    editDataSidebar(data) {
      data.accountId = 12
      this.sidebarData = data;
      this.toggleDataSidebar(true);
    },
    //********************* Event functions */
    populateDataOperatorType() {
        var self = this;
        if (self.operatorTypeSelect) {
            self.formData.operatorTypeId = self.operatorTypeSelect.value;
            self.formData.operatorType = self.operatorTypeSelect.label;
          }
      },

     cancelAdd() {
        this.isListHidden = false,
        this.isInfoHidden = true;
        this.initValues();
        this.GetGrid();
    },
   addVue() {
        this.initValues();
        this.isInfoHidden = false;
        this.isListHidden = true
        this.GetDropdownOperatorType();
        // this.$refs.wizard.navigateToTab(0)

    },

    saveOperatorParameters(data)
    {
        debugger
    },
     initValues() {
          var self = this;
          self.$refs.wizard.navigateToTab(0)
     },
    //********************* SQL events */
    formSubmitted() {
          var self = this;
          var myobject={};
    },
    addGrid(data) {   
      const self = this;
      data.accountId = 12 ;// JSON.stringify(data),
      self.showLoadingOnElement("gridTable", 1, "radius");
      var onSuccess = function(response) {
        self.GetGrid();
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
        self.successNotify();
      };
       self.$ajaxPost(
        self,
         "Operators",
        data,
        onSuccess,
        onFinally
      );
    },
     updateGrid(data) {
       const self = this;
      self.showLoadingOnElement("gridTable", 1, "radius");
      var onSuccess = function(response) {
        self.GetGrid();
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
         self.successNotify();
      };
      self.$ajaxPut(
        self,
        "Operators/" + data.id,
        data,
        onSuccess,
        onFinally
      );
    },
    deleteGrid(data) {
      const self = this;
      self.showLoadingOnElement("gridTable", 1, "radius");
      
      var onSuccess = function(response) {
        self.GetGrid();
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
         self.successNotify();
      };
      self.$ajaxDelete(
        self,
        "Operators/" + data.id + "?AccountId=" +  data.accountId,
        onSuccess,
        onFinally
      );
    },
    GetGrid() {
      const self = this;
      self.showLoadingOnElement("gridTable", 1, "radius");
      var onSuccess = function(response) {
        self.operatorData = response.data;
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
      };
      self.$ajaxGet(
        self,
         "Operators/12?Username=csd",
        onSuccess,
        onFinally
      );
    },
    GetDropdownOperatorType() { 
          const self = this;
          var onSuccess = function(response) {
              self.operatorTypeData = response.data.map((item) => {return {label: item.name, value: item.id} });
          };
          var onFinally = function() {
            self.$vs.loading.close(self.$refs.gridTable.$el);
          };
          self.$ajaxGet(
            self,
            "OperatorTypes/GetOperatorTypedropdown?AccountId=12&username=csd",
            onSuccess,
            onFinally
          );    
        },
    toggleDataSidebar(val = false) {
      this.operatorSidebar = val;
    },
  }
};
     
</script>
